import { Link } from 'react-router-dom';
import { useEffect } from 'react';
import { animateCards } from '../animations';

function Projects() {
  useEffect(() => {
    animateCards();
  }, []);

  const projects = [
    {
      id: 'resume-parser',
      title: 'Resume Parser',
      description: 'NLP-based parser extracting resume data using NER and FastAPI.',
      tech: ['Python', 'FastAPI', 'Hugging Face', 'Pandas'],
    },
    {
      id: 'ai-interview-bot',
      title: 'AI Interview Bot',
      description: 'NLP model for interview question generation and response evaluation.',
      tech: ['Python', 'Hugging Face', 'NLP'],
    },
    {
      id: 'ppe-detection',
      title: 'PPE Detection System',
      description: 'Real-time safety detection using YOLO and RTSP feeds.',
      tech: ['Python', 'YOLO', 'OpenCV', 'Flask'],
    },
    {
      id: 'sales-prediction',
      title: 'Sales Prediction Model',
      description: 'Flask-based web app for sales forecasting with Random Forest.',
      tech: ['Python', 'Flask', 'Random Forest', 'Pandas'],
    },
    {
      id: 'health-safety',
      title: 'Health and Safety Detection',
      description: 'CCTV-based detection of PPE, fire, and hygiene issues.',
      tech: ['Python', 'YOLO', 'Flask', 'WebSocket'],
    },
  ];

  return (
    <section className="container mx-auto p-4">
      <h2 className="text-3xl font-bold font-montserrat mb-4 text-center">Projects</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {projects.map((project) => (
          <Link to={`/project/${project.id}`} key={project.id} className="card p-4 rounded shadow hover:scale-105 transition-transform">
            <h3 className="text-xl font-poppins">{project.title}</h3>
            <p className="font-roboto">{project.description}</p>
          </Link>
        ))}
      </div>
    </section>
  );
}

export default Projects;