import { Link } from 'react-router-dom';
import anime from 'animejs/lib/anime.es.js';
import { useEffect } from 'react';
import lLogo from '../assets/images/logo/l-logo.png';
import dLogo from '../assets/images/logo/d-logo.png';

function Header({ toggleTheme, theme }) {
  useEffect(() => {
    anime({
      targets: '.header',
      opacity: [0, 1],
      duration: 800,
      easing: 'easeOutQuad',
    });
  }, []);

  return (
    <header className="header flex items-center justify-between max-w-screen-xl mx-auto py-2 px-4">
      <div className="flex items-center">
        <img
          src={theme === 'dark' ? dLogo : lLogo}
          alt="Logo"
          className="h-12 w-12 mr-2"
        />
        <h1 className="text-3xl font-bold font-poppins py-2">Suryansh</h1>
      </div>
      <div className="checkbox-wrapper-5 ml-auto">
        <div className="check">
          <input
            id="check-5"
            type="checkbox"
            checked={theme === 'light'}
            onChange={toggleTheme}
            readOnly
            style={{
              background: theme === 'dark'
                ? 'linear-gradient(45deg, #dedede, #ffffff)'
                : 'linear-gradient(90deg, #f19af3, #f099b5)'
            }}
          />
          <label htmlFor="check-5"></label>
        </div>
      </div>
    </header>
  );
}

export default Header;