import Header from '../components/Header';
import Home from '../components/Home';
import Skills from '../components/Skills';
import Projects from '../components/Projects';
import Contact from '../components/Contact';
import FallingImage from '../components/FallingImage';
import CircularButton from '../components/CircularButton';
import lLogo from '../assets/images/logo/l-logo.png';
import dLogo from '../assets/images/logo/d-logo.png';
import { useEffect } from 'react';

function HomePage({ toggleTheme, theme }) {
  useEffect(() => {
    const favicon = document.getElementById('favicon');
    if (favicon) {
      if (theme === 'dark') {
        favicon.href = '/src/assets/images/logo/d-logo.png';
      } else {
        favicon.href = '/src/assets/images/logo/l-logo.png';
      }
    }
  }, [theme]);

  return (
    <div className="relative">
      <div className="hero-section min-h-[120vh] relative">
        {theme === 'dark' && <FallingImage />}
        <Header toggleTheme={toggleTheme} theme={theme} />
        <div className="flex items-center h-full pl-10">
          <div className="text-left z-100" style={{ zIndex: 100, mixBlendMode: 'lighten', backgroundColor: 'transparent', position: 'relative' }}>
            <h1 className="text-4xl font-bold font-montserrat">Hey. I'm Suryansh Singh</h1>
            <p className="text-xl font-roboto">a Machine Learning Engineer specializing in NLP, computer vision, and AI solutions.</p>
          </div>
        </div>
      </div>
      {/* About Me section - only visible in dark mode */}
      {theme === 'dark' && (
        <section className="about-me-section w-full min-h-[40vh] flex flex-col md:flex-row items-end justify-between px-8 py-12" style={{ background: 'transparent' }}>
          <div className="flex-1 flex flex-col justify-start">
            <h2
              className="text-6xl md:text-7xl"
              style={{
                fontFamily: 'PilcrowRounded, PilcrowRounded-Heavy, sans-serif',
                fontWeight: 900,
                color: '#F27830',
                textAlign: 'left',
                marginBottom: '2rem',
              }}
            >
              About Me
            </h2>
          </div>
          <div className="flex-1 flex flex-col justify-end items-end">
            <p
              className="text-lg md:text-xl max-w-xl text-right"
              style={{
                fontFamily: 'Archivo_Complete, Archivo-Regular, sans-serif',
                fontWeight: 400,
                color: '#fff',
                lineHeight: 1.7,
              }}
            >
              Im a Machine Learning Engineer with a passion for crafting innovative AI solutions in NLP, computer vision, and beyond. Im skilled in Python, PyTorch, TensorFlow, and tools like Hugging Face and OpenCV, with hands-on experience building projects like an AI Interview Model and a Resume Parser that solve real-world problems. I love blending creativity with tech, which youll see in the dynamic design of this portfoliothink falling animations and sleek color schemes. Driven by problem-solving and a knack for leadership, Im excited to push the boundaries of AI innovation and connect with those who share that vision.
            </p>
          </div>
        </section>
      )}
      <div className="pt-[45rem]">
        <Home />
        <Skills />
        <Projects />
        <Contact />
      </div>
      <CircularButton />
    </div>
  );
}

export default HomePage; 