import anime from 'animejs/lib/anime.es.js';

export const animateIntro = () => {
  anime({
    targets: '.intro',
    opacity: [0, 1],
    translateY: [50, 0],
    duration: 1000,
    easing: 'easeOutQuad',
  });
};

export const animateCards = () => {
  anime({
    targets: '.card',
    opacity: [0, 1],
    translateY: [20, 0],
    delay: anime.stagger(200),
    duration: 800,
    easing: 'easeOutQuad',
  });
};

export const animateSkills = () => {
  anime({
    targets: '.skill',
    opacity: [0, 1],
    translateX: [-20, 0],
    delay: anime.stagger(100),
    duration: 600,
    easing: 'easeOutQuad',
  });
};