import { useEffect, useRef } from 'react';
import anime from 'animejs/lib/anime.es.js';
import uShape from '../assets/images/U-shape.png';
import photo from '../assets/images/photo.png';

function FallingImage() {
  const imgRef = useRef();
  const photoRef = useRef();

  useEffect(() => {
    const animate = () => {
      if (imgRef.current && photoRef.current) {
        anime({
          targets: [imgRef.current, photoRef.current],
          translateY: ['-120%', '0%'],
          easing: 'easeOutQuad',
          duration: 1500,
          complete: () => {
            if (imgRef.current && photoRef.current) {
              imgRef.current.style.position = 'absolute';
              photoRef.current.style.position = 'absolute';
            }
          },
        });

        anime({
          targets: photoRef.current,
          opacity: [0, 1],
          easing: 'easeOutQuad',
          duration: 2000,
          delay: 650,
        });
      }
    };
    animate();
  }, []);

  return (
    <>
      <img
        ref={photoRef}
        src={photo}
        alt="Photo"
        className="photo"
        style={{ position: 'fixed', top: 0, left: '50%', transform: 'translateX(-50%)', zIndex: 49 }}
        width={500}
        height={220}
      />
      <img
        ref={imgRef}
        src={uShape}
        alt="U-shape"
        className="u-shape"
        style={{ position: 'fixed', top: 0, left: '50%', transform: 'translateX(-50%)', zIndex: 50 }}
        width={600}
        height={940}
      />
      <style>
        {`
          .u-shape {
            background-color: transparent !important;
            -webkit-background-color: transparent !important;
            -moz-background-color: transparent !important;
            mix-blend-mode: lighten;
            filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.3));
          }
          .photo {
            opacity: 0;
          }
          .profile-photo {
            position: relative;
          }
        `}
      </style>
    </>
  );
}

export default FallingImage;