import { useEffect, useRef } from 'react';
import anime from 'animejs/lib/anime.es.js';
import emailjs from '@emailjs/browser';

function Contact() {
  const formRef = useRef();

  useEffect(() => {
    anime({
      targets: '.contact',
      opacity: [0, 1],
      translateY: [20, 0],
      duration: 800,
      easing: 'easeOutQuad',
    });
  }, []);

  const sendEmail = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        'YOUR_SERVICE_ID', // Replace with your EmailJS Service ID
        'YOUR_TEMPLATE_ID', // Replace with your EmailJS Template ID
        formRef.current,
        'YOUR_PUBLIC_KEY' // Replace with your EmailJS Public Key
      )
      .then(
        () => {
          alert('Message sent successfully!');
          formRef.current.reset();
        },
        (error) => {
          alert('Failed to send message. Please try again.');
          console.error('EmailJS error:', error);
        }
      );
  };

  return (
    <section className="contact container mx-auto p-4">
      <footer className="p-4 mt-8">
        <div className="flex flex-col md:flex-row justify-between items-start w-full">
          <div className="text-left mb-4 md:mb-0 md:w-1/2 flex flex-col items-start">
            <div className="w-full max-w-md flex flex-col items-center">
              <h2 className="text-2xl font-bold font-montserrat mb-4 text-center">Contact Me</h2>
              <div className="space-y-2">
                <p className="font-roboto"> Phone: +91-XXX-XXX-XXXX</p>
                <p className="font-roboto"> Email: suruswork395@example.com</p>
                <p className="font-roboto"> LinkedIn: www.linkedin.com/in/suryansh-singh-5857a0263</p>
                <p className="font-roboto"> GitHub: github.com/CodingSuru</p>
              </div>
            </div>
          </div>
          <div className="md:w-1/2 w-full flex flex-col items-end">
            <div className="w-full max-w-lg flex flex-col items-center">
      <h2 className="text-3xl font-bold font-montserrat mb-4 text-center">Get in Touch</h2>
              <form ref={formRef} onSubmit={sendEmail} className="space-y-4">
        <input
          type="text"
          name="from_name"
          placeholder="Your Name"
          required
                  className="w-full p-2 rounded border font-roboto"
        />
        <input
          type="email"
          name="from_email"
          placeholder="Your Email"
          required
                  className="w-full p-2 rounded border font-roboto"
        />
        <input
          type="text"
          name="subject"
          placeholder="Subject"
          required
                  className="w-full p-2 rounded border font-roboto"
        />
        <textarea
          name="message"
          placeholder="Enter Message Here"
          required
                  className="w-full p-2 rounded border font-roboto"
          rows="5"
        ></textarea>
        <button
          type="submit"
          className="w-full p-2 rounded font-poppins transition"
        >
          Send Message
        </button>
      </form>
            </div>
          </div>
        </div>
      </footer>
    </section>
  );
}

export default Contact;