function ProjectPage({ title, description, tech, impact }) {
  return (
    <section className="container mx-auto p-4">
      <h2 className="text-3xl font-bold font-montserrat mb-4">{title}</h2>
      <p className="font-roboto mb-4">{description}</p>
      <h3 className="text-xl font-poppins mb-2">Technologies</h3>
      <ul className="list-disc pl-5 font-roboto mb-4">
        {tech.map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
      <h3 className="text-xl font-poppins mb-2">Impact</h3>
      <p className="font-roboto">{impact}</p>
    </section>
  );
}

export default ProjectPage;