import React from 'react';

function Skills() {
  return (
    <section className="container mx-auto p-4">
      <h2 className="text-3xl font-bold font-montserrat mb-4">Skills</h2>
      <div className="grid grid-cols-2 gap-4">
        <div>
          <h3 className="text-xl font-bold font-roboto">Programming</h3>
          <ul className="list-disc pl-5">
            <li>Python</li>
            <li>Java</li>
            <li>Kotlin</li>
          </ul>
        </div>
        <div>
          <h3 className="text-xl font-bold font-roboto">ML Frameworks</h3>
          <ul className="list-disc pl-5">
            <li>PyTorch</li>
            <li>TensorFlow</li>
            <li>Hugging Face</li>
          </ul>
        </div>
        <div>
          <h3 className="text-xl font-bold font-roboto">NLP</h3>
          <ul className="list-disc pl-5">
            <li>NER</li>
            <li>Text Extraction</li>
            <li>Tokenization</li>
          </ul>
        </div>
        <div>
          <h3 className="text-xl font-bold font-roboto">Computer Vision</h3>
          <ul className="list-disc pl-5">
            <li>OpenCV</li>
            <li>YOLO</li>
            <li>Image Processing</li>
          </ul>
        </div>
        <div>
          <h3 className="text-xl font-bold font-roboto">Tools</h3>
          <ul className="list-disc pl-5">
            <li>Git</li>
            <li>Flask</li>
            <li>FastAPI</li>
          </ul>
        </div>
        <div>
          <h3 className="text-xl font-bold font-roboto">Soft Skills</h3>
          <ul className="list-disc pl-5">
            <li>Leadership</li>
            <li>Problem-Solving</li>
            <li>Collaboration</li>
          </ul>
        </div>
      </div>
    </section>
  );
}

export default Skills;