import { useParams } from 'react-router-dom';
import ProjectPage from '../components/ProjectPage';
import Header from '../components/Header';

function ProjectDetail() {
  const { id } = useParams();

  const projects = {
    'resume-parser': {
      title: 'Resume Parser',
      description: 'An NLP-based parser extracting structured data from resumes using NER and custom rules, deployed via FastAPI with Excel output.',
      tech: ['Python', 'FastAPI', 'Hugging Face', 'Pandas'],
      impact: 'Automated resume data extraction for efficient HR processing.',
    },
    'ai-interview-bot': {
      title: 'AI Interview Bot',
      description: 'NLP model for generating interview questions and evaluating responses, providing feedback for candidate improvement.',
      tech: ['Python', 'Hugging Face', 'NLP'],
      impact: 'Improved candidate preparation with automated, scalable feedback.',
    },
    'ppe-detection': {
      title: 'PPE Detection System',
      description: 'Real-time safety violation detection using YOLO and RTSP camera feeds, integrated with a Flask web interface.',
      tech: ['Python', 'YOLO', 'OpenCV', 'Flask'],
      impact: 'Enhanced workplace safety with optimized detection.',
    },
    'sales-prediction': {
      title: 'Sales Prediction Model',
      description: 'Flask-based web app for sales forecasting using Random Forest Regressor, supporting diverse input formats.',
      tech: ['Python', 'Flask', 'Random Forest', 'Pandas'],
      impact: 'Enabled accurate sales predictions, streamlining business planning.',
    },
    'health-safety': {
      title: 'Health and Safety Detection',
      description: 'CCTV-based detection of PPE, fire, and hygiene issues using YOLOv5/v8, integrated with Flask and WebSocket.',
      tech: ['Python', 'YOLO', 'Flask', 'WebSocket'],
      impact: 'Improved safety compliance with real-time monitoring.',
    },
  };

  const project = projects[id] || {};

  return (
    <div>
      <Header />
      <ProjectPage
        title={project.title}
        description={project.description}
        tech={project.tech}
        impact={project.impact}
      />
    </div>
  );
}

export default ProjectDetail;